@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap');


.navbar {
    width: 100%;
    z-index: 10;
    background: #b4692b00;
    position: relative;
    text-align: center;
    margin-bottom: -2.4rem;
}

.navbar img {
  margin-left: 1rem;
}


.nav-box {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .6rem 0;
    background-color: #c27d2efd;
    border-radius: 10px;    
}


.nav-box ul {
  margin: auto;
}

.navbar-logo {
  z-index: 9999999;
}

.navbar .icon {
  font-size: 1.4rem;
}

.navbar .logo .icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--primary-color);
}

.hamburger {
    display: none;
    padding: 1rem;
}

.navbar .nav-menu {
    height: 100%;
    align-items: center;
    display: flex;
    padding: 1rem;
}
.navbar li {
    width: max-content;
    padding: .3rem 1rem;
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
}


.navbar li a {
  color: rgb(224, 226, 225);
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-weight: bolder;
  font-size: 1rem;
}

.navbar li a:hover {
  color: rgb(9, 132, 141);
}

  .menu-item .sub__menus li a {
    color: #000000;
    font-size: 13px;
    padding: -1rem ;
  }

  .menu-item .sub__menus li a:hover {
    color: rgb(255, 123, 0) !important;
  }

  li .submenu-header {
    font-size: 14px;
    color: rgb(36, 131, 27);
    font-weight: bolder;
    text-decoration: underline;
    cursor: pointer;
  }


/* -----------------------------------------------------> SUB MENU */
.sub__menus {
    position: absolute;
    display: none;
    background: white;
    border: 1px solid lavender;
    border-radius: 10px;
    width: auto;
    left: -4rem;
    padding: 2px 0 0 0;
    margin-top: 6px;
    z-index: 1000;
  }



  /* -----------------------------------------------------> DROPDOWN ARROW */
  .sub__menus__arrows {
    position: relative;
  }

  .sub__menus__arrows svg, .sub__menus__arrows__full svg {
    padding-top: 0px;
    position: absolute;
    top: 7px;
  }


.logo-hide {
  display: none;
}

@media screen and (max-width: 1200px) {


    .navbar .hamburger {
        display: block;
        z-index: 15;
    }

    .navbar li a {
      color: rgb(250, 250, 250);
    }

    .navbar .nav-menu {
        position: absolute;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        top: 0;
        left:-100%;
        align-items: center;
        justify-content: center;
        background: rgb(82, 182, 221);
        transition: 0.5s ease-in;
        color: white;
    }

    .navbar .active {
        left: 0;
    }

    .navbar li {
        font-size: 1.5rem;
    }

    .navbar button {
        font-size: 1.8rem;
    }

    .menu-item .sub__menus li a {
        font-size: 14px;
      }

      li .submenu-header {
        font-size: 14px;
      }

      #submenu-item li {
        padding: .2rem 1rem;
      }

      .sub__menus {
        width: auto;
      }

  .logo-hide {
      display: block;
  }

  .nav-box {
    border-radius: 0;    
    background-color: #fff;
}

.sub__menus__arrows svg, .sub__menus__arrows__full svg {

  top: 15px;
}


}


